* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Informasi Kulineria */
.info {
    margin-top: 20px; 
    padding: 40px;
    text-align: left;
    background: #ffffff;
    border-radius: 20px;
    margin-bottom: 40px;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 5%);
}

.info h1 {
    margin-bottom: 20px;
    font-size: 48px;
    font-weight: 700;
    color: #C43E4A;
}

.desc {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 20px;
}

.descLine {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 18px;
    line-height: 1.6;
}

/* Detail Reservasi */
.reservasiDetails {
    padding: 40px;
    background: #ffffff;
    border-radius: 20px;
    margin-bottom: 40px;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 5%);
}
.reservasiDetails h2{
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 10px;
}
.reservasiDetailsWrapper{
    display: flex;
}
.reservasiDetailsWrapper .desc{
    width: 80%;
}
.reservasiDetails .batal{
    align-items: center;
    padding: 10px 10px;
}
.reservasiDetails .batal button {
    width: 100%;
    padding: 18px;
    background-color: #C43E4A;
    color: #ffffff;
    border: none;
    border-radius: 12px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
    max-height: 80%;
}

.reservasiDetails .batal button:hover{
    background-color: #a83640;
}

/* Buat Reservasi */
.buatReservasi {
    padding: 40px;
    background: #ffffff;
    border-radius: 20px;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 5%);
}
.buatReservasi h2 {
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 10px;
}

.subtitle {
    font-size: 2.5vh;
    color: #777;
    margin-bottom: 30px;
}

.reservasiWrapper {
    display: flex;
    width: 100%;
    gap: 40px;
    justify-content: space-between;
    flex-wrap: nowrap;
    align-items: flex-start;    
}

/* Card */
.card {
    display: flexbox;
    border-radius: 15px;
    padding-right: 60px;
}

.card h3 {
    min-width: auto;
    font-size: 24px;
    font-weight: 600;
    color: #2c3e50;
    margin-bottom: 20px;
    padding-bottom: 15px;
    border-bottom: 3px solid #C43E4A;
}

/* Denah */
.denah {
    flex: 1;
}
.denahImgWrapper img{
    width: 100%;
}
/* Pilihan Meja */
.seatsList {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 30px;
}

.seatOption {
    position: relative;
    cursor: pointer;
}

.seatOption input[type="radio"] {
    display: none;
}

.seat {
    width: 100%;
    height: 100px;
    background: #ffffff;
    border: 3px solid #C43E4A;
    border-radius: 12px;
    font-size: 24px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s;
    user-select: none;
}


/* Hover box meja yang tidak unavailable */
.seatOption:not(.unavailable):hover .seat {
    background: #fff0f1;
}

/* Box meja yang diselect */
.seatOption input[type="radio"]:checked + .seat {
    background: #C43E4A;
    color: #ffffff;
}

/* Meja yang unavailable */
.seatOption.unavailable {
    pointer-events: none;
}
.seatOption.unavailable .seat {
    background: #cccccc;
    color: #848484;
    border-color: #848484;
    cursor: not-allowed;
}

/* Confirm button */
.confirm-btn {
    width: 100%;
    padding: 18px;
    background-color: #C43E4A;
    color: #ffffff;
    border: none;
    border-radius: 12px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
    transition: 0.2s;
    text-decoration:none;
}

.confirm-btn:hover {
    background-color: #A83640;
}

.confirm-btn:disabled {
    background: #cccccc;
    cursor: not-allowed;
}

/* Pop Up Konfirmasi Batal Reservasi*/
.popUpBatal {
    display: none;
    position: fixed;
    background: rgba(0,0,0,0.3);
    backdrop-filter: blur(2px);
    justify-content: center;
    align-items: center;
    inset: 0;
    z-index: 1000;
}

.popUpContent {
    background: white;
    padding: 25px 30px;
    border-radius: 16px;
    text-align: center;
    box-shadow: 0 10px 30px rgba(0,0,0,0.15);
}

.popUpContent h3 {
    font-size: 22px;
    margin-bottom: 10px;
}

.popUpContent p {
    color: #444;
    margin-bottom: 20px;
}

.popUpButtons {
    display: flex;
    gap: 15px;
}

.popUpButtons button {
    flex: 1;
    padding: 12px;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
}

.buttonYa {
    background-color: #C43E4A;
    color: white;
}

.buttonYa:hover {
    background-color: #A83640;
}

.buttonTidak {
    background-color: #ddd;
}

.buttonTidak:hover {
    background-color: #c6c6c6;
}
