* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.info {
  text-align: left;
  background: #fff;
  border-radius: 20px;
  margin-top: 20px;
  margin-bottom: 40px;
  padding: 40px;
  box-shadow: 0 5px 5px #0000000d;
}

.info h1 {
  color: #c43e4a;
  margin-bottom: 20px;
  font-size: 48px;
  font-weight: 700;
}

.desc {
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.descLine {
  align-items: center;
  gap: 12px;
  font-size: 18px;
  line-height: 1.6;
  display: flex;
}

.reservasiDetails {
  background: #fff;
  border-radius: 20px;
  margin-bottom: 40px;
  padding: 40px;
  box-shadow: 0 5px 5px #0000000d;
}

.reservasiDetails h2 {
  margin-bottom: 10px;
  font-size: 36px;
  font-weight: 700;
}

.reservasiDetailsWrapper {
  display: flex;
}

.reservasiDetailsWrapper .desc {
  width: 80%;
}

.reservasiDetails .batal {
  align-items: center;
  padding: 10px;
}

.reservasiDetails .batal button {
  color: #fff;
  cursor: pointer;
  background-color: #c43e4a;
  border: none;
  border-radius: 12px;
  width: 100%;
  max-height: 80%;
  padding: 18px;
  font-size: 18px;
  font-weight: 600;
}

.reservasiDetails .batal button:hover {
  background-color: #a83640;
}

.buatReservasi {
  background: #fff;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 5px 5px #0000000d;
}

.buatReservasi h2 {
  margin-bottom: 10px;
  font-size: 36px;
  font-weight: 700;
}

.subtitle {
  color: #777;
  margin-bottom: 30px;
  font-size: 2.5vh;
}

.reservasiWrapper {
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 40px;
  width: 100%;
  display: flex;
}

.card {
  display: flexbox;
  border-radius: 15px;
  padding-right: 60px;
}

.card h3 {
  color: #2c3e50;
  border-bottom: 3px solid #c43e4a;
  min-width: auto;
  margin-bottom: 20px;
  padding-bottom: 15px;
  font-size: 24px;
  font-weight: 600;
}

.denah {
  flex: 1;
}

.denahImgWrapper img {
  width: 100%;
}

.seatsList {
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin-bottom: 30px;
  display: grid;
}

.seatOption {
  cursor: pointer;
  position: relative;
}

.seatOption input[type="radio"] {
  display: none;
}

.seat {
  user-select: none;
  background: #fff;
  border: 3px solid #c43e4a;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100px;
  font-size: 24px;
  font-weight: 700;
  transition: all .2s;
  display: flex;
}

.seatOption:not(.unavailable):hover .seat {
  background: #fff0f1;
}

.seatOption input[type="radio"]:checked + .seat {
  color: #fff;
  background: #c43e4a;
}

.seatOption.unavailable {
  pointer-events: none;
}

.seatOption.unavailable .seat {
  color: #848484;
  cursor: not-allowed;
  background: #ccc;
  border-color: #848484;
}

.confirm-btn {
  color: #fff;
  cursor: pointer;
  background-color: #c43e4a;
  border: none;
  border-radius: 12px;
  width: 100%;
  padding: 18px;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  transition: all .2s;
}

.confirm-btn:hover {
  background-color: #a83640;
}

.confirm-btn:disabled {
  cursor: not-allowed;
  background: #ccc;
}

.popUpBatal {
  backdrop-filter: blur(2px);
  z-index: 1000;
  background: #0000004d;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.popUpContent {
  text-align: center;
  background: #fff;
  border-radius: 16px;
  padding: 25px 30px;
  box-shadow: 0 10px 30px #00000026;
}

.popUpContent h3 {
  margin-bottom: 10px;
  font-size: 22px;
}

.popUpContent p {
  color: #444;
  margin-bottom: 20px;
}

.popUpButtons {
  gap: 15px;
  display: flex;
}

.popUpButtons button {
  cursor: pointer;
  border: none;
  border-radius: 10px;
  flex: 1;
  padding: 12px;
  font-size: 18px;
  font-weight: 600;
}

.buttonYa {
  color: #fff;
  background-color: #c43e4a;
}

.buttonYa:hover {
  background-color: #a83640;
}

.buttonTidak {
  background-color: #ddd;
}

.buttonTidak:hover {
  background-color: #c6c6c6;
}
